            <div class="empty" v-if="list.length === 0">暂时还没有参赛方案~</div>
            <div class="task-dir" v-if="list.length>0" v-for="item in list">
              <div class="task-tit">{{item.name}}</div>
              <div class="task-list" >
                <div class="task-item" v-for="sItem in item.list">
                  <div class="delete" @click="handleDeleteItem(item.sItem)">
                    <img src="@/common/images/icon_delete.png" alt="">
