                  </div>
                  <img src="@/common/images/icon_topic.png" alt="">
                  <div class="desc">{{sItem.filename}}</div>
                </div>
              </div>
            </div>
          </div>
          <div class="tab-item" v-if="currentIndex==1">
            <div class="tab-body">
              <div class="dir-change">
                <div class="item">
                  <div class="s-tit">应用挑战</div>
                </div>
                <div class="item" @click="handleLink('factory')">
                  <img src="@/common/images/icon_factory.png" alt="">
                  <div class="s-tit">工业方向</div>
                  <div class="desc">lenovo.com</div>
                </div>
                <div class="item" @click="handleLink('jd')">
                  <img src="@/common/images/video.jpg" alt="">
                  <div class="s-tit">语音方向</div>
                  <div class="desc">jd.com</div>
                </div>
                <div class="item" @click="handleLink('safe')">
                  <img src="@/common/images/type_icon_protection.png" alt="">
                  <div class="s-tit">安全方向</div>
                  <div class="desc">antiy.com</div>
                </div>
              </div>
            </div>
            <div class="tab-body">
              <div class="dir-change dir-start">
                <div class="item">
                  <div class="s-tit">双创激荡</div>
                </div>
                <div class="item">
                  <div class="s-tit" @click="handleLinkDouble">自主创新创业类</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer-s">
      <img src="../../common/images/yf_logo.png" alt="">
      <span>北京亚非兄弟科技有限公司技术支持</span>
    </div>
    <modal :isShow="showModalStatus" @close="handleCloseModal" @submit="handleDelete"></modal>
  </div>
