<script>
import storage from 'good-storage';
import http from '@/http/index';
import NavBar from '@/components/NavBar.vue';
import Modal from './Modal';
export default {
  components: {
    NavBar,
    Modal
  },
  data() {
    return {
      currentIndex: 0,
      list: [],
      showModalStatus: false,
      currentId: 0,

    }
  },
  mounted(){
    let USS = storage.get('token');
     http.get('/v1api/filelist', {params: {USS}}).then(res => res.data).then((res) => {
        let tempArr = [];
        let tempList = [];
        let tempObj = {
          name: '',
          list: []
        };
        let tempSafe = {
          name: '',
          list: []
        };
        let tempJd = {
          name: '',
          list: []
        };
        let tempDouble = {
          name: '',
          list: []
        };
        if(res.errcode === 0) {
          for (var i = 0; i< res.data.length; i++) {
            let item = res.data[i];
            switch(item.tasktype){
              case 'factory':
                tempObj.name = item.taskname;
                tempObj.list.push(item);
                // tempArr.push(tempObj);
                break;
              case 'safe':
                tempSafe.name = item.taskname;
                tempSafe.list.push(item);
                // tempArr.push(tempSafe);
                break;
              case 'jd':
                tempJd.name = item.taskname;
                tempJd.list.push(item);
                // tempArr.push(tempJd);
                break;
              case 'double':
                tempDouble.name = item.taskname;
                tempDouble.list.push(item);
                // tempArr.push(tempDouble);
                break;
            }
          }
          if(tempObj.name){
            tempArr.push(tempObj);
          }
          if(tempSafe.name){
            tempArr.push(tempSafe);
          }
          if(tempJd.name) {
            tempArr.push(tempJd);
          }
          if(tempDouble.name){
            tempArr.push(tempDouble);
          }
          console.log(tempArr);
         this.list = tempArr;
       } else {

       }
     })
  },
  methods: {
    handleLink(dir) {
      this.$router.push({path: '/upload', query: {dir}})
    },
    handleCloseModal() {
      this.showModalStatus = false;
    },
    handleLinkDouble() {
      this.$router.push('/double')
    },
    handleDelete() {
      let USS = storage.get('token');
      http.get('/v1api/plandelete', {params: {USS, id: this.currentId}}).then(res => res.data).then((res) => {
       console.log(res.data);
       if(res.errcode === 0) {
         this.list = res.data;
         this.showModalStatus = false;
       } else {

       }
      })
    },
    handleDeleteItem(id) {
      this.currentId = id;
      this.showModalStatus = true;
    },
    handleChangeTab(idx){
      this.currentIndex = idx;
    }
  }
}
</script>
